import m from "mithril";
import ripple from "polythene-ripple";
import { createProps, createContent, theme } from "polythene-button";

const renderer = m;

const updater = vnode => (attrs, value) => (
  vnode.state[attrs] = value,
  setTimeout(m.redraw, 0)
);

const keys = {
  class:       "class",
  onblur:      "onblur",
  onclick:     "onclick",
  onfocus:     "onfocus",
  onkeydown:   "onkeydown",
  onkeyup:     "onkeyup",
  onmousedown: "onmousedown",
  onmouseout:  "onmouseout",
  onmouseover: "onmouseover",
  onmouseup:   "onmouseup",
};
const keyer = key => (
  console.log("key", key, keys[key]), 
  keys[key]
);

const oninit = vnode => 
  vnode.state = Object.assign(
    vnode.state, 
    {
      focus:     false,
      mouseover: false,
      inactive:  false
    }
  );

const view = vnode => {
  const updateState = updater(vnode);
  return renderer(
    vnode.attrs.element || "a",
    createProps(vnode, { renderer, updateState, keyer, ripple }),
    [
      vnode.attrs.before,
      createContent(vnode, { renderer, updateState, keyer, ripple }),
      vnode.attrs.after
    ]
  );
};

export default {
  theme,
  oninit,
  view
};
